.header{
    color: white;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.punkLogo{
    width: 200px
}

.header > div{
    margin: 10px;
}
.searchBar{
    background-color: #1c1c1e;
    height: 50px;
    flex: 1;
    display: flex;
    border-radius: 50px;
    align-items: center;
}
.searchIconContainer{
    margin:10px
}
.searchInput{
    background-color: transparent;
    outline: none;
    border:none;
    width:100%;
    color: #feee;
    font-size: 16px;
}
.headerItems{
    display: flex;
}
.headerItems >p {
    margin:10px
}
.headerActions{
    display: flex;
    align-items: center;
}
.headerActions>div{
    margin: 10px;
}
.themeSwitchContainer{
    display: flex;
    background-color: #1c1c1e;
    padding: 15px;
    border-radius: 50px;
    justify-content: center;
    align-items: center;
    object-fit: contain;
    cursor: pointer;
} 
.themeSwitchContainer >img{
    height: 25px;
}
.loginButton{
    background: linear-gradient(to right, #59f9b7,#66feea);
    padding: 15px 40px;
    color: black;
    border-radius: 50px;
}
