.collectionCard{
    color: white;
    background-color: #1c1c1e;
    border-radius: 20px;
    overflow: hidden;
    width: 300px;
    height: 500px;
    margin-right: 30px;
    transition-property: all;
    transition: .1s ease-in-out;
}

.collectionCard>img{
    width: 100%;
}
.wethImage{
    height:23px;
    width: 15px;
}
.price{
    margin-left: 5px;
    font-weight: 600;
}
.priceContainer{
    display: flex;
    align-items: center;
    margin-top: 10px;
    object-fit: contain;
}
.details{
    padding: 20px;
}
.name{
    font-size: 20px;
    font-weight: 900;
}
.id{
    color: #a1a5b0;
    font-size: 18px;
    font-weight: 600;
}
.collectionCard:hover{
    transform: scale(105%);
}